<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar cisternas</h3>
            <button @click="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white text-sm hover:bg-orange-500 transition duration-300">
                Crear cisterna
            </button>
        </div>
        <div class="overflow-x-auto shadow-md sm:rounded-lg">
            <TableUnidades :listUnidades="listUnidades" @eliminar-unidad="eliminarUnidad" @editar-unidad="editarUnidad" />
        </div>
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="saveDatos" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar esta unidad?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idUnidad="idUnidad" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModaError v-if="openModalError" title="Advertencia" mensaje="Se debe rellenar todos los campos"
                @close-modal="closeModalError" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openSuccessEdit" title="Logrado" mensaje="Se ha editado correctamente"
                @close-modal="closeSuccessEdit" />
        </transition>
    </section>
</template>
<script>
import { ref, onMounted } from 'vue';
import { listarUnidades, crearUnidades, eliminarUnidades, editarUnidades } from '../services/UnidadesService';
import TableUnidades from '../components/unidades/TableUnidades';
import ModalCreate from '../components/unidades/ModalCreate';
import ModalDelete from '../components/generales/ModalDelete.vue';
import ModalEditar from '../components/unidades/ModalEditar';
import ConfirmEdit from '../components/generales/ConfirmEdit.vue';
import ModaError from '../components/generales/ModalError.vue';
import store from '../store'

export default ({
    name: "AdminUnidades",
    components: {
        TableUnidades, ModalCreate,
        ModalDelete, ModalEditar,
        ModaError, ConfirmEdit
    },
    setup() {
        const openModalV = ref(false);
        const openModalEdit = ref(false);
        const openModalError = ref(false);
        const confirmDelete = ref(false);
        const openSuccessEdit = ref(false);
        const listUnidades = ref([]);
        const idUnidad = ref('')
        const openModal = () => {
            openModalV.value = true;
        };

        const closeModal = () => {
            openModalV.value = false;
        }

        const closeSuccessEdit = () => {
            openSuccessEdit.value= false;
            window.location.reload();
        }

        const eliminarUnidad = (idunidad) => {
            idUnidad.value = idunidad
            confirmDelete.value = true;
        }

        const aceptarEliminar = () => {
            eliminarUnidades(idUnidad.value)
                .then(response => {
                    console.log(response.data)
                    confirmDelete.value = false;
                    idUnidad.value = '';
                    window.location.reload();
                })
                .catch((error) => {
                    console.log(error)
                })
        }

        const cancelarEliminar = () => {
            confirmDelete.value = false;
        }

        const cancelarEditar = () => {
            openModalEdit.value = false;
        }
        const editarUnidad = (idunidad) => {
            idUnidad.value = idunidad;
            openModalEdit.value = true;
        }

        const aceptarEditar = (dataformulario) => {
            const placa = dataformulario.placa;
            const codcisterna = dataformulario.codcisterna;
            const capacidad = dataformulario.capacidad;
            const id = dataformulario.id;
            if (placa == '' || codcisterna == '' || capacidad == '') {
                openModalEdit.value = false;
                openModalError.value = true;
            } else {
                editarUnidades(placa, codcisterna, capacidad, id)
                    .then(response => {
                        console.log(response.data)
                        openModalEdit.value = false;
                        openSuccessEdit.value= true;
                    })
            }
        }

        const closeModalError = () => {
            openModalError.value = false;
        }

        const saveDatos = (dataformulario) => {
            const placa = dataformulario.placa;
            const codcisterna = dataformulario.codcisterna;
            const capacidad = dataformulario.capacidad;
            if (placa == '' || codcisterna == '' || capacidad == '') {
                openModalV.value = false;
                openModalError.value = true;
            } else {
                crearUnidades(placa, codcisterna, capacidad, store.state.codempresa)
                    .then(response => {
                        console.log(response.data)
                        openModalV.value = false;
                        window.location.reload();
                    })
            }
        }
        onMounted(() => {
            listarUnidades(store.state.codempresa)
                .then(response => {
                    listUnidades.value = response.data.unidades
                })
        })
        return {
            listUnidades,
            openModal,
            openModalV,
            openModalError,
            openSuccessEdit,
            closeModal,
            closeSuccessEdit,
            closeModalError,
            saveDatos,
            confirmDelete,
            eliminarUnidad,
            aceptarEliminar,
            cancelarEliminar,
            cancelarEditar,
            editarUnidad,
            openModalEdit,
            idUnidad,
            aceptarEditar
        }
    }
})
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}
</style>